* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

html {
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 20px;
}

body {
  background-color:  rgba(194, 183, 183, 0.3);
}

.text-important {
  color:rgb(79, 79, 206);
  font-weight:900;
}


.todo-container {
  padding: 1rem;
  width: 80%;
  height: auto;
  margin: 2rem  auto;
  border: 1px solid whitesmoke;
  background-color:whitesmoke;
  border-radius: 6px;
  box-shadow: 0 0 5px rgb(155, 150, 150);
}

.todo-header {
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-bottom: 1rem;
}

.heading {
 font-weight: bold;
 color: #000;
}

.more {
  font-weight: bold;
  color:#333;
}


.todo-form-search-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.input-search {
  width: 58%;
  padding:  0.4rem 1rem;
  border-radius: 5px;
  border:1px solid silver;
  outline: none;
  background-image: url(./images/lupa.png);
  background-position: 99% 100%;
  background-repeat: no-repeat;
  background-size: contain;
  transition: background-position 0.3s ease;
}
.input-search:focus {
  border-color:rgb(121, 121, 190);
  background-position: 200% 0;
  box-shadow: 0 0 10px rgb(121, 121, 190);
}

.todo-form-filter {
  width: 40%;
  display: flex;
  justify-content: space-around;
}

.btn-filter {
  font-weight: bold;
  width: 33.3%;
  padding:  0.4rem 1rem;
  border-radius: 5px;
  border:1px solid silver;
  outline: none;
  cursor: pointer;
  margin: 0 0.1rem;
  transition: box-shadow 0.3s ease;
  transition: border-color 0.3s ease;
  transition: background-color 0.3s ease;
  color: #000;
  text-align: center;
}
.btn-filter:focus {
  border-color:rgb(102, 102, 197);
  box-shadow: 0 0 10px rgb(121, 121, 190);
  background-color: rgb(102, 102, 197);
}

.btn-filter:hover {
  box-shadow: 0 0 10px rgb(121, 121, 190);
  background-color:rgb(79, 79, 206);
}

.todo-form-add {
 display: flex;
 justify-content: space-between;
 align-items: center;
}

.input-text {
  width: 58%;
  padding:  0.4rem 1rem;
  border-radius: 5px;
  border:1px solid silver;
  outline: none;
}

.input-text:focus {
  border-color:rgb(121, 121, 190);
  background-position: 200% 0;
  box-shadow: 0 0 10px rgb(121, 121, 190);
}

.btn-add {
  font-weight: bold;
  width: calc(100% - 58%);
  margin-left: 0.2rem;
  padding:  0.4rem 1rem;
  border-radius: 5px;
  border:1px solid silver;
  outline: none;
  cursor: pointer;
  background-color: rgb(121, 121, 190);
  border-color:rgb(102, 102, 197);
  transition: box-shadow 0.3s ease;
  transition: background-color 0.3s ease;
  color: #ffffff;
}

.btn-add:hover {
  box-shadow: 0 0 10px rgb(121, 121, 190);
  background-color:rgb(79, 79, 206);
}

.list {
  margin-top:0.5rem;
}

.list-item {
  width: 100%;
  list-style: none;
  padding: 0.4rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color:#ffff;
  margin:0.1rem 0;
  border:1px solid silver;
  border-radius: 6px;
  transition: border-color 0.3s ease;
}

.list-item:hover {
   border-color:rgb(79, 79, 206);
}


.btn-remove {
  margin: 0 0.1rem;
  padding:0.3rem;
  background-color: #fff;
  font-weight: bold;
  border: 1px solid rgb(235, 91, 91);
  cursor: pointer;
  color:rgb(226, 99, 99);
  border-radius: 5px;
  transition: color 0.3s ease;
  transition: background-color 0.3s ease;
  transition: box-shadow 0.3s ease;
  box-shadow:0 0 5px rgb(226, 99, 99);
}

.btn-remove:hover {
  background-color: rgb(235, 91, 91);
  color:#fff;
  box-shadow: 0 0 10px rgb(235, 91, 91);
}

.btn-important {
  margin: 0 0.1rem;
  padding:0.3rem 0.5rem;
  background-color: #fff;
  color: rgb(27, 211, 27);
  font-weight: bold;
  border: 1px solid rgb(27, 211, 27);
  cursor: pointer;
  border-radius: 5px;
  transition: color 0.3s ease;
  transition: background-color 0.3s ease;
  transition: box-shadow 0.3s ease;
  box-shadow:0 0 5px rgb(27, 211, 27);
}

.btn-important:hover {
  background-color: rgb(27, 211, 27);
  color:#fff;
  box-shadow: 0 0 10px rgb(27, 211, 27);
}

.important {
  background-color: rgb(27, 211, 27);
  color: #fff;
}

.active-filter {
  background-color:rgb(27, 211, 27);
  box-shadow:0 0 5px rgb(27, 211, 27);
}